.form-field {
  box-sizing: border-box;
  height: 48px;
  display: block;
  width: 100%;
  border: 2px solid transparent;
  background-color: #F7F8FF;
  border-radius: 8px;
  padding:  14px 16px;
  color: #000;

  &::placeholder {
  font-size: 14px;
  line-height: 20px;
  color: #8F99A8;
  }

  &:hover {
    border-color: #CCD0E0;;
  }

  &:focus {
    border-color: $colorBlue;
    outline: none;
  }

  &--textarea {
    height: 144px;
    resize: none;

  }
}
